<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card  :flat="isSubPage" class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18" >
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto " >
                            <div class="" >
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Tb Pelanggaran Details</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md" >
            <div class="container">
                <div class="row q-col-gutter-x-md">
                    <div class="col comp-grid" >
                        <q-card  :flat="isSubPage" class=" nice-shadow-18">
                            <div >
                                <div class="relative-position" style="min-height:100px">
                                    <template v-if="!loading && item">
                                        <div class="row q-col-gutter-x-md">
                                            <div class="col">
                                                <q-item>
                                                    <q-item-section>
                                                        <q-item-label caption>Nis: </q-item-label>
                                                        <q-item-label class="text-bold">{{ item.nisn }}</q-item-label>
                                                    </q-item-section>
                                                </q-item>
                                                <q-separator></q-separator>
                                                <q-item>
                                                    <q-item-section>
                                                        <q-item-label caption>Nama Siswa: </q-item-label>
                                                        <q-item-label class="text-bold">
                                                            <q-btn v-if="item.student_id" :to="`/tb_siswa/view/${item.student_id}`" padding="xs" color="blue-1" unelevated text-color="blue" no-caps >
                                                                <q-icon name="fas fa-eye" size="xs"  class="q-mr-xs"></q-icon>  {{ item.tb_siswa_std_name }}
                                                            </q-btn>
                                                        </q-item-label>
                                                    </q-item-section>
                                                </q-item>
                                                <q-separator></q-separator>
                                                <q-item>
                                                    <q-item-section>
                                                        <q-item-label caption>Kelas: </q-item-label>
                                                        <q-item-label class="text-bold">
                                                            <q-btn v-if="item.class_id" :to="`/tb_kelas/view/${item.class_id}`" padding="xs" color="blue-1" unelevated text-color="blue" no-caps >
                                                                <q-icon name="fas fa-eye" size="xs"  class="q-mr-xs"></q-icon>  {{ item.tb_kelas_class_name }}
                                                            </q-btn>
                                                        </q-item-label>
                                                    </q-item-section>
                                                </q-item>
                                                <q-separator></q-separator>
                                                <q-item>
                                                    <q-item-section>
                                                        <q-item-label caption>Wali Kelas: </q-item-label>
                                                        <q-item-label class="text-bold">
                                                            <q-btn v-if="item.teacher_id" :to="`/tb_guru/view/${item.teacher_id}`" padding="xs" color="blue-1" unelevated text-color="blue" no-caps >
                                                                <q-icon name="fas fa-eye" size="xs"  class="q-mr-xs"></q-icon>  {{ item.tb_guru_teacher_name }}
                                                            </q-btn>
                                                        </q-item-label>
                                                    </q-item-section>
                                                </q-item>
                                                <q-separator></q-separator>
                                                <q-item>
                                                    <q-item-section>
                                                        <q-item-label caption>Orang Tua: </q-item-label>
                                                        <q-item-label class="text-bold">
                                                            <q-btn v-if="item.wali_id" :to="`/tb_wali/view/${item.wali_id}`" padding="xs" color="blue-1" unelevated text-color="blue" no-caps >
                                                                <q-icon name="fas fa-eye" size="xs"  class="q-mr-xs"></q-icon>  {{ item.tb_wali_parent_name }}
                                                            </q-btn>
                                                        </q-item-label>
                                                    </q-item-section>
                                                </q-item>
                                                <q-separator></q-separator>
                                                <q-item>
                                                    <q-item-section>
                                                        <q-item-label caption>Katagori Pelanggaran: </q-item-label>
                                                        <q-item-label class="text-bold">
                                                            <q-btn v-if="item.type_id" :to="`/tb_tipe_pelanggaran/view/${item.type_id}`" padding="xs" color="blue-1" unelevated text-color="blue" no-caps >
                                                                <q-icon name="fas fa-eye" size="xs"  class="q-mr-xs"></q-icon>  {{ item.tb_tipe_pelanggaran_violation_name }}
                                                            </q-btn>
                                                        </q-item-label>
                                                    </q-item-section>
                                                </q-item>
                                                <q-separator></q-separator>
                                                <q-item>
                                                    <q-item-section>
                                                        <q-item-label caption>Note: </q-item-label>
                                                        <q-item-label class="text-bold">{{ item.note }}</q-item-label>
                                                    </q-item-section>
                                                </q-item>
                                                <q-separator></q-separator>
                                                <q-item>
                                                    <q-item-section>
                                                        <q-item-label caption>Tgl/Waktu Kejadian: </q-item-label>
                                                        <q-item-label class="text-bold">
                                                            <q-chip v-if="item.reported_on" dense size="13px" :label="item.reported_on | relativeDate">
                                                            <q-tooltip
                                                            content-class="bg-accent"
                                                            transition-show="scale"
                                                            transition-hide="scale"
                                                            >
                                                            {{ item.reported_on | humanDatetime}}
                                                            </q-tooltip>
                                                            </q-chip>
                                                        </q-item-label>
                                                    </q-item-section>
                                                </q-item>
                                                <q-separator></q-separator>
                                                <q-item>
                                                    <q-item-section>
                                                        <q-item-label caption>Foto Kejadian: </q-item-label>
                                                        <q-item-label class="text-bold">{{ item.foto_kejadian }}</q-item-label>
                                                    </q-item-section>
                                                </q-item>
                                                <q-separator></q-separator>
                                                <q-card-actions class="row q-col-gutter-xs justify-end">
                                                    <q-btn icon="fas fa-bars" padding="xs" round flat color="grey">
                                                        <q-menu auto-close transition-show="flip-right"  transition-hide="flip-left" self="center middle" anchor="center middle">
                                                            <q-list dense rounded nav>
                                                                <q-item link clickable v-ripple :to="`/tb_pelanggaran/edit/${item.id}`">
                                                                    <q-item-section>
                                                                        <q-icon color="positive"  size="sm" name="fas fa-edit"></q-icon>
                                                                    </q-item-section>
                                                                    <q-item-section>Edit</q-item-section>
                                                                </q-item>
                                                                <q-item link clickable v-ripple @click="deleteItem(item.id)">
                                                                    <q-item-section>
                                                                        <q-icon color="negative"  size="sm" name="fas fa-times"></q-icon>
                                                                    </q-item-section>
                                                                    <q-item-section>Delete</q-item-section>
                                                                </q-item>
                                                            </q-list>
                                                        </q-menu>
                                                    </q-btn>
                                                </q-card-actions>
                                            </div>
                                        </div>
                                    </template>
                                    <template v-if="loading">
                                        <div class="q-pa-sm text-center">
                                            <q-inner-loading :showing="loading">
                                                <q-spinner :size="40" color="primary" indeterminate></q-spinner>
                                            </q-inner-loading>
                                        </div>
                                    </template>
                                </div>
                            </div>
                        </q-card>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
	import { PageMixin } from "../../mixins/page.js";
	import { ViewPageMixin } from "../../mixins/viewpage.js";
	import { mapActions, mapGetters, mapState } from "vuex";
	export default {
		name: 'viewTbpelanggaranPage',
		components: {
		},
		mixins: [PageMixin, ViewPageMixin ],
		props: {
			pageName: {
				type : String,
				default : 'tb_pelanggaran',
			},
			idName: {
				type: String,
				default: 'id',
			},
			routeName : {
				type : String,
				default : 'tb_pelanggaranview',
			},
			pagePath: {
				type : String,
				default : 'tb_pelanggaran/view',
			},
			apiPath: {
				type : String,
				default : 'tb_pelanggaran/view',
			},
		},
		data() {
            return {
				item: {
					default :{
					},
				},
			}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Tb Pelanggaran Details"
				}
			},
			currentRecord: {
				get: function () {
					return this.$store.getters["tb_pelanggaran/currentRecord"];
				},
				set: function (value) {
					this.$store.commit("tb_pelanggaran/setCurrentRecord", value);
				},
			},
		},
		meta() {
			return {
				title: this.pageTitle
			}
		},
		methods: {
			...mapActions("tb_pelanggaran", [ "fetchRecord", "deleteRecord"]),
		},
		watch: {
			$route (to, from){
				//only fetch data when navigated to this page
				if(to.name == this.routeName){
					this.load();
				}
			},
		},
		async mounted() {
		},
		async created() {
		},
	};
</script>
<style scoped>
</style>
